<template>
  <div id="app" @touchstart="touchstart" @touchend="touchend">
    <!-- 一级路由出口 -->
    <keep-alive>
      <router-view
        @tanslate-song="tanslateSong"
        @songcard-list="songList"
        @searcard-list="searchList"
        :currentSong="currentSong"
      ></router-view>
    </keep-alive>

    <!-- 播放器 边栏 -->
    <PlayBar v-if="select()" :currentSong="currentSong" :flag="flag"></PlayBar>

    <searchPlay v-if="searchTag" :currentSong="currentSong" :flag="flag"></searchPlay>
  </div>
</template>
<script>
import PlayBar from "@/components/PlayBar";
import searchPlay from "@/components/searchPlay";

export default {
  components: {
    PlayBar,
    searchPlay
  },
  data: function() {
    return {
      currentSong: null,
      touchstartX: 0,
      flag: false,
      searchTag: false
    };
  },

  methods: {
    touchstart: function(event) {
      // console.log("开始", event.changedTouches[0].clientX);
      this.touchstartX = event.changedTouches[0].clientX;
      this.touchstartY = event.changedTouches[0].clientY;
    },
    touchend: function(event) {
      // console.log("结束", event.changedTouches[0].clientX);

      // 判断左滑动 返回上一页
      if (event.changedTouches[0].clientX - this.touchstartX > 50) {
        console.log("从左到右");
        this.touchstartX = 0;
        this.touchstartY = 0;
        this.routerBack();
      }

      if (event.changedTouches[0].clientX - this.touchstartX < -50) {
        console.log("从右到左");
        this.routerForward();
      }
    },
    routerBack: function() {
      this.$router.back();
    },
    routerForward: function() {
      this.$router.forward();
    },
    songList: function($event) {
      this.flag = true;
      this.searchTag = false;
      this.currentSong = $event;
    },
    tanslateSong: function($event) {
      this.flag = false;
      this.searchTag = false;
      this.currentSong = $event;
    },
    searchList: function($event) {
      this.searchTag = true;
      this.currentSong = $event;
    },
    select: function() {
      if (!this.searchTag) {
        return this.currentSong;
      } else {
        return null;
      }
    }
  },
  watch: {
    // 当前播放id变化
    currentSongId: function() {}
  }
};
</script>
<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  padding-bottom: 50px;
}
</style>
