<template>
  <div class="palylist">
    <HomeLink></HomeLink>
    <searchInput @s-input="getSearch"></searchInput>
    <searchContent
      v-for="(item, index) in searchSongs"
      :key="index"
      :songItem="item"
      @searcard-list="$emit('searcard-list', $event)"
       :currentSong="currentSong"
    ></searchContent>
  </div>
</template>

<script>
import HomeLink from "@/components/HomeLink.vue";
import searchInput from "@/components/searchInput.vue";
import searchContent from "@/components/searchContent.vue";
export default {
  props: {
    currentSong: Object
  },
  components: {
    HomeLink,
    searchInput,
    searchContent
  },
  data() {
    return {
      searchSongs: null
    };
  },
  methods: {
    getSearch: function(v) {
      this.axios
        .get("/search", {
          params: {
            keywords: v
          }
        })
        .then(response => {
          console.log(response);
          this.searchSongs = response.data.result.songs;
          // console.log(this.searchSongs);
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>
